// @ts-ignore
import * as swaggerData from './swagger.json'

const headStyle = "swimlane;childLayout=stackLayout;horizontal=1;startSize=30;horizontalStack=0;resizeParent=1;resizeParentMax=0;resizeLast=0;collapsible=1;marginBottom=0;strokeColor=#9673a6;fillColor=#e1d5e7;fontColor=#660066;"
const itemStyle = "text;dashed=1;strokeColor=#A680B8;fontColor=#634C6E;spacingLeft=10;"
const itemHeight = 30
const itemWidth = 250

const getXML = () => {
    // @ts-ignore
    const keys: any[] = Object.entries(swaggerData.components.schemas)

    const data = keys.map((item, index) => {

        return {
            type: item[1].hasOwnProperty('enum') ? 'enum' : 'DTO',
            name: item[0],
            props: item[1].properties || item[1].enum,
            desc: item[1].description,
            line: Math.ceil((index + 1) / 10)
        }
    })

    const DtoSrc = data.filter(item => item.type === 'DTO').filter(item => item.name.includes('taff'))

    const EnumSrc = data.filter(item => item.type === 'enum')

    const DTOdata = DtoSrc.map((item, index) => {
        return {
            ...item,
            line: Math.ceil((index + 1) / 10)
        }
    })

    const EnumData = EnumSrc.map((item, index) => {
        return {
            ...item,
            line: Math.ceil((index + 1) / 10)
        }
    })

    const getChunk = (arr: any[], size: number): any[] => {

        return arr.reduce((p,c)=>{
            if(p[p.length-1].length == size){
                p.push([]);
            }

            p[p.length-1].push(c);
            return p;
        }, [[]]);
    }

    const chunkDTO = getChunk(DTOdata, 10)

    const getXmlDTO = (arr) => arr.map((dto, indexDTO) => {
        const itemID = `${indexDTO + 1}-line${dto.line}-item`
        const index = indexDTO + 1

        const itemX = (itemWidth * index) + (30 * index)
        const itemY = dto.line * 400

        const container = `<mxCell id="${itemID}" value="${dto.name}" style="${headStyle}" vertex="1" parent="1" >`
        const geoHeader =
            `<mxGeometry x="${itemX}" y="${itemY}" width="${itemWidth}" height="${itemHeight * 3}" as="geometry">` +
            `<mxRectangle x="${itemX}" y="${itemY}" width="${itemWidth}" height="${itemHeight}" as="alternateBounds"/>` +
            `</mxGeometry>`
        const end = `</mxCell>`

        const head = container + geoHeader + end

        // @ts-ignore
        const blocks = Object.entries(dto.props).map((prop, indexProp) => {
            const name = prop[0]
            const type = prop[1].type
            const itemY = indexProp + 30

            const block =
                `<mxCell id="${itemID}-${indexProp}-prop" value="${name}: ${type}" style="${itemStyle}" vertex="1" parent="${itemID}">`
            const geoBlock =
                `<mxGeometry y="${itemY}" width="${itemWidth}" height="${itemHeight}" as="geometry"/>`
            const end =
                `</mxCell>`

            return block + geoBlock + end
        })

        const props = blocks.reduce((acc, block) => {
            return acc + block
        }, '')

        return head + props
    })

    const getXmlLine = (arr) => arr.reduce((acc, dto) => {
        return acc + dto
    }, '')


    const test = chunkDTO.map((item) => {
        const xml = getXmlDTO(item)
        return getXmlLine(xml)
    })

    console.log(getXmlLine(test))

}

getXML()